body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin-left: 25%;
  margin-right: 25%;
}

.Display{
    background-color: #444;
    color: #fff;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-right: 30px;
    position: relative;
    width: 93%;
}

.Result {
    text-align: right;
    position: absolute;
    bottom: 0;
    right: 0;
}

/*db.users.update({'email':'kurewallet@gmail.com'},{$set:{'balance':0}})*/

.ButtonPanel {
    width: 100%;
    height: auto;
}

.Button {
    float: left;
    /*width: 24.7%;*/
    text-align: center;
    padding-top: 3rem;
    padding-bottom: 3rem;

    border-width: 1px;
    border-style: solid;
    border-color: #bababa;

    background-color: #e0e0e0;

    font-weight: bold;
}
